import { formCheck } from '@/config/checkout'
import { ALERT_TIME } from '@/config/config'
import { mapGetters, mapMutations } from 'vuex'
export default {
  name: "login",
  data () {
    return {
      mobile: '', // 手机号
      pwd: '', // 密码
      verify_code: '', //验证码
      img_code: '', // 图形验证码
    };
  },
  computed: {
    ...mapGetters([
      'haveLogin',
      'userInfo'
    ])
  },
  created () {
    this.get_img_code()
  },
  methods: {
    // 获取图形验证码
    get_img_code () {
      this.$Api({
        api_name: 'kkl.user.getImgCode',
      }, (err, data) => {
        if (!err) {
          this.img_code = data.data.url
        } else {
          this.$msg(err.error_msg, 'error', 1500)
        }
      })
    },
    // 更换图形验证码
    change_code_img () {
      this.img_code = ''
      this.get_img_code()
    },
    // 注册跳转
    register () {
      this.$router.push({
        path: '/register'
      })
    },
    // 忘记密码跳转
    getPwd () {
      this.$router.push({
        path: '/forgetPwd'
      })
    },
    // 登录
    login () {
      let checkItem = [{
        reg: 'mobile',
        val: this.mobile,
        errMsg: '请输入正确的手机号'
      }, {
        reg: 'noData',
        val: this.pwd,
        errMsg: '密码不能为空'
      }, {
        reg: 'noData',
        val: this.verify_code,
        errMsg: '校验码不能为空'
      }]
      if (formCheck(checkItem).result) {
        this.$Api({
          api_name: 'kkl.user.login',
          mobile: this.mobile,
          password: this.$MD5.hex_md5(this.pwd),
          code: this.verify_code
        }, (err, data) => {
          if (!err) {
            this.$toast({ text: '登录成功' });
            this.$router.replace({
              path: '/'
            })
            localStorage.setItem('loginStatu',1)
            this.$Api({ api_name: 'kkl.user.getUserInfo' }, (erra, res) => {
              if (!erra) {
                this.setUser(res.data)
              } else {
                this.$msg(erra.error_msg, 'cancel', 'middle', 1500)
              }
            })
          } else {
            if (data.code === 40051) {
              this.img_code = ''
              this.get_img_code()
            }
            this.$msg(err.error_msg, 'cancel', 'middle', 1500)
          }
        })
      } else {
        this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
      }
    },
    ...mapMutations({
      setUser: 'SET_USER',
      delUser: 'DEL_USER'
    })
  }
}



