
import { formCheck} from '@/config/checkout'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
components:{
Confirm
},
name: 'sendPacket',
data () {
return {
balance: '', // 银行余额
list: [{
title: '手机短信'
}],
type_list: [{
title: '普通红包'
},{
title: '手气红包'
}],
currentIndex: 0,
current_index: 0,
verify_code: '',
mobile: '',
remark: '',
count: '',
number: '',
sendAuthCode: false,
auth_time: 0,
show: false,
title: '已生成红包链接',
content_url: '',
text_pro: '单个金额'
}
},
created() {
this.get_user_info()
},
methods: {

confirm() {
this.$native.send_paste(this.content_url)
},

get_user_info() {
this.$Api({
api_name: 'kkl.user.getUserInfo',
}, (erra, res) => {
console.log('请求结果', erra, res)
if (!erra) {
this.balance = res.data.frozen_money
this.mobile = res.data.mobile
} else {
this.$msg(erra.error_msg, 'cancel', 'middle', 1500)
}
})
},

get_verify() {
let checkItem = [{
reg: 'mobile',
val: this.mobile,
errMsg: '请输入正确的手机号'
}]
if (formCheck(checkItem).result) {
this.$Api({
api_name: 'kkl.user.sendVerifyCode',
mobile: this.mobile
}, (err, data) => {
if (!err) {
this.$msg(data.data, 'success', 'middle',1500)
this.sendAuthCode = true
this.auth_time = 60
var auth_timetimer =  setInterval(()=>{
this.auth_time--;
if(this.auth_time<=0){
this.sendAuthCode = false
clearInterval(auth_timetimer);
}
}, 1000);
} else {
this.$msg(err.error_msg, 'cancel', 'middle', 1500)
}
})
} else {
this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
}
},
change_index(index) {
this.currentIndex = index
},
changeIndex(index) {
this.current_index = index
if(index == 0) {
this.text_pro = '单个金额'
} else {
this.text_pro = '总金额'
}
},

produce() {
let checkItem = [{
reg: 'noData',
val: this.number,
errMsg: '金额不能为空'
}, {
reg: 'noData',
val: this.count,
errMsg: '数量不能为空'
}, {
reg: 'noData',
val: this.verify_code,
errMsg: '短信验证码不能为空'
},{
reg: 'noData',
val: this.remark,
errMsg: '标题不能为空'
}]
if (formCheck(checkItem).result) {
this.$Api({
api_name: 'kkl.user.sendRedPacket',
type: this.current_index + 1,
total_money: this.number,
num: this.count,
code: this.verify_code,
title: this.remark
}, (err, data) => {
if (!err) {





let jiami_id = data.data

document.documentElement.scrollTop = document.body.scrollTop = 0
this.show = true
this.content_url = `${window.location.host}/#/index?jiami_id=${jiami_id}`
} else {
this.$msg(err.error_msg, 'cancel', 'middle', 1500)
}
})
} else {
this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
}
},
}
}



