export default {
  components: {

  },
  props: {
    relief_list: {
      type: Array,
      default: ''
    },
    copy_list: {
      type: Array,
      default: ''
    },
    check_boolean: {
      type: Boolean,
      default: false
    }
  },
  name: 'record',
  data () {
    return {

    }
  },
  created () {

  },
  methods: {
    check (index) {
      this.$emit('check', index)
    },
    check_all () {
      this.$emit('check_all')
    },
    onCopy () {
      this.$emit('onCopy')
    },
    onError () {
      this.$msg('复制失败', 'cancel', 'middle', 1500)
    }
  }
}



