export default {
    components:{
        
    },
    name: 'forgetPwd',
    data () {
        return {
            list: [{
                title: '忘记登录密码',
            },{
                title: '忘记安全密码',
            },{
                title: '忘记银行密码'
            }]
        }
    },
    created() {
        
    },
    methods: {
        turn_page(index) {
            let idx = Number(index + 1)
            this.$router.push({
                path: '/retrievePwd',
                query: {
                    type: idx
                }
            })
        }
    }
}



