  var xhr = null;
  if (window.XMLHttpRequest) {
    xhr = new window.XMLHttpRequest();
  } else { // ie
    xhr = new ActiveObject("Microsoft")
  }

  xhr.open("GET", "/", false) //false不可变
  xhr.send(null);
