export default {
    components:{
        
    },
    name: 'dailyRelief',
    data () {
        return {
            list: [{
                title: '等级',
            },{
                title: '经验起步',
            },{
                title: '经验截止',
            },{
                title: '救济乐豆',
            }],
            relief_list: []
        }
    },
    created() {
        this.get_level_list()
    },
    methods: {
        // 获取等级列表
        get_level_list() {
            this.$Api({
                api_name: 'kkl.user.levelList'
            }, (err, data) => {
                if (!err) {
                    this.relief_list = data.data.level_list
                } else {
                    this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        receive() {
            this.$Api({
                api_name: 'kkl.user.getRelief'
            }, (err, data) => {
                if (!err) {
                    this.$msg(data.data, 'success', 'middle', 1500)
                } else {
                    this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
    }
}



