import { formCheck} from '@/config/checkout'
export default {
    components:{
        
    },
    name: 'retrievePwd',
    data () {
        return {
            list: [{
                title: '手机短信'
            },
            // {
            //     title: '微信验证'
            // },
            ],
            currentIndex: 0,
            type: 0,
            placeholder: '',
            img_code: '', // 图形验证码路径
            mobile: '', // 手机号
            code: '',  // 图形验证码
            verify_code: '', // 短信验证码
            new_pwd: '', //新密码
            sendAuthCode: false,
            auth_time: 0,
        }
    },
    created() {
        this.get_img_code()
        this.type = this.$route.query.type
        if (this.type == 1) {
            this.placeholder = '请输入新的密码'
        } else if(this.type == 3) {
            this.placeholder = '请输入新的银行密码'
        } else {
            this.placeholder = '请输入新的安全密码'
        }
    },
    methods: {
        // 发送验证码
        get_verify() {
            let checkItem = [{
                reg: 'mobile',
                val: this.mobile,
                errMsg: '请输入正确的手机号'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.user.sendVerifyCode',
                    mobile: this.mobile
                }, (err, data) => {
                    if (!err) {
                        this.$msg(data.data, 'success','middle', 1500)
                        this.sendAuthCode = true
                        this.auth_time = 60
                        var auth_timetimer =  setInterval(()=>{
                            this.auth_time--;
                            if(this.auth_time<=0){
                                this.sendAuthCode = false
                                clearInterval(auth_timetimer);
                            }
                        }, 1000);
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        },
        // 获取图形验证码
        get_img_code() {
            this.$Api({
                api_name: 'kkl.user.getImgCode',
            }, (err, data) => {
                if (!err) {
                    this.img_code = data.data.url
                } else {
                    this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        // 更换图形验证码
        change_code_img() {
            this.img_code = ''
            this.get_img_code()
        },
        // 选择验证方式
        change_index(index) {
            this.currentIndex = index
        },
        // 提交
        submit() {
            let checkItem = [{
                reg: 'mobile',
                val: this.mobile,
                errMsg: '填写正确手机号'
            }, {
                reg: 'noData',
                val: this.code,
                errMsg: '填写图形验证码'
            }, {
                reg: 'noData',
                val: this.verify_code,
                errMsg: '验证码不能为空'
            }, {
                reg: 'noData',
                val: this.new_pwd,
                errMsg: '新密码不能为空'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.user.findPassword',
                    mobile: this.mobile,
                    verify_code: this.verify_code,
                    code: this.code
                }, (err, data) => {
                    if (!err) {
                        this.$Api({
                            api_name: 'kkl.user.resetPassword',
                            mobile: this.mobile,
                            new_password: this.$MD5.hex_md5(this.new_pwd),
                            re_password: this.$MD5.hex_md5(this.new_pwd),
                            type: this.type
                        }, (err, data) => {
                            if (!err) {
                                this.$msg(data.data, 'success', 'middle', 1500)
                                setTimeout(() => {
                                    if (this.type == 1) {
                                        this.$native.back()
                                        // this.$native.native_dismiss()
                                    } else {
                                        this.$router.go(-1)
                                    }
                                },1500)
                            } else {
                                this.$msg(err.error_msg, 'cancel','middle', 1500)
                            }
                        })
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        }
    }
}



