import { formCheck} from '@/config/checkout'
export default {
    components:{
        
    },
    name: 'bindTencent',
    data () {
        return {
            list: [{
                title: '手机短信'
            },
            // {
            //     title: '微信验证'
            // },
            ],
            currentIndex: 0,
            qq: '',
            verify_code: '',
            sendAuthCode: false,
            auth_time: 0,
            mobile: ''
        }
    },
    created() {
        this.get_user_info()
    },
    methods: {
        get_user_info() {
            this.$Api({
                api_name: 'kkl.user.getUserInfo',
            }, (erra, res) => {
                if (!erra) {
                    this.mobile = res.data.mobile
                } else {
                    this.$msg(erra.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        change_index(index) {
            this.currentIndex = index
        },
        // 发送验证码
        get_verify() {
            let checkItem = [{
                reg: 'mobile',
                val: this.mobile,
                errMsg: '请输入正确的手机号'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.user.sendVerifyCode',
                    mobile: this.mobile
                }, (err, data) => {
                    if (!err) {
                        this.$msg(data.data, 'success', 'middle', 1500)
                        this.sendAuthCode = true
                        this.auth_time = 60
                        var auth_timetimer =  setInterval(()=>{
                            this.auth_time--;
                            if(this.auth_time<=0){
                                this.sendAuthCode = false
                                clearInterval(auth_timetimer);
                            }
                        }, 1000);
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        },
        // 立刻绑定
        editor_now() {
            let checkItem = [{
                reg: 'noData',
                val: this.qq,
                errMsg: 'QQ号码不能为空'
            }, {
                reg: 'noData',
                val: this.verify_code,
                errMsg: '验证码不能为空'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.user.bindQQ',
                    qq: this.qq,
                    verify_code: this.verify_code
                }, (err, data) => {
                    if (!err) {
                        this.$msg(data.data, 'success', 'middle', 1500)
                        this.qq = ''
                        this.verify_code = ''
                        setTimeout(function() {
                            this.$router.go(-1)
                        }, 1500)
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        }
    }
}



