import { fiveIndex } from '@/config/mixin.js'
export default {
  name: "rankList",
  mixins: [fiveIndex],
  data() {
    return {
      list: [
        {
          title: "今日排行"
        },
        {
          title: "昨日排行"
        },
        {
          title: "七日排行"
        }
      ],
      currentIndex: 0,
      rank_list: []
    };
  },
  created() {
    this.get_rank_list();
  },
  methods: {
    get_rank_list() {
      this.$Api(
        {
          api_name: "kkl.index.rankList"
        },
        (err, data) => {
          if (!err) {
            let res = data.data;
            if (this.currentIndex == 0) {
              this.rank_list = res.today_list;
            } else if (this.currentIndex == 1) {
              this.rank_list = res.yesterday_list;
            } else {
              this.rank_list = res.sevenday_list;
            }
          } else {
            this.$msg(err.error_msg, "cancel", "middle", 1500);
          }
        }
      );
    },
    change_index(index) {
      this.rank_list = [];
      this.currentIndex = index;
      this.get_rank_list();
    }
  }
};



