import { fiveIndex } from '@/config/mixin.js'
export default {
  name: "gameIndex",
  mixins: [fiveIndex],
  data() {
    return {
      gameTypes: []
    };
  },
  methods: {
    gameInfo(gameItem){
       this.$router.push({
         path: '/gameInfo',
         query: {
           game_type_id: gameItem.game_type_id,
           game_type_name: gameItem.game_type_name,
           table_type: gameItem.table_type
         }
       })
    }
  },
  created() {
    this.$Api(
      {
        api_name: "kkl.game.getGameTypeList",
        is_app: 1
      },
      (err, data) => {
        if (!err) {
          this.gameTypes = data.data;
        }
      }
    );
  }
};



