import {
  ALERT_TIME
} from '@/config/config'
import {
  formCheck
} from '@/config/checkout'
export default {
  name: "login",
  data () {
    return {
      mobile: '', // 手机号
      pwd: '', // 密码
      realname: '', // 昵称
      referee: '', // 推荐人
      verify_code: '', // 短信验证码
      code: '', // 图形验证码
      sendAuthCode: false,
      auth_time: 0,
      img_code: '', // 图形验证码
    };
  },
  created () {
    this.get_img_code()
  },
  methods: {
    // 获取图形验证码
    get_img_code () {
      this.$Api({
        api_name: 'kkl.user.getImgCode',
      }, (err, data) => {
        if (!err) {
          this.img_code = data.data.url
        } else {
          this.$msg(err.error_msg, 'error', 1500)
        }
      })
    },
    // 更换图形验证码
    change_code_img () {
      this.img_code = ''
      this.get_img_code()
    },
    // 发送验证码
    get_verify () {
      let checkMobile = [{
        reg: 'mobile',
        val: this.mobile,
        errMsg: '请输入正确的手机号'
      }, {
        reg: 'noData',
        val: this.code,
        errMsg: '校验码不能为空'
      }]
      if (formCheck(checkMobile).result) {
        this.$Api({
          api_name: 'kkl.user.sendVerifyCode',
          mobile: this.mobile
        }, (err, data) => {
          if (!err) {
            console.log(data)
            this.$msg(data.data, 'success', 'middle', 1500)
            this.sendAuthCode = true
            this.auth_time = 60
            var auth_timetimer = setInterval(() => {
              this.auth_time--;
              if (this.auth_time <= 0) {
                this.sendAuthCode = false
                clearInterval(auth_timetimer);
              }
            }, 1000);
          } else {
            this.img_code = ''
            this.get_img_code()
            this.$msg(err.error_msg, 'cancel', 'middle', 1500)
          }
        })
      } else {
        this.$msg(formCheck(checkMobile), 'cancel', 'middle', 1500)
      }
    },
    // 注册
    register () {
      let checkItem = [{
        reg: 'mobile',
        val: this.mobile,
        errMsg: '请输入正确的手机号'
      }, {
        reg: 'pwd',
        val: this.pwd,
        errMsg: '密码请输入6-20位字母和数字'
      }, {
        reg: 'noData',
        val: this.realname,
        errMsg: '昵称不能为空'
      }, {
        reg: 'noData',
        val: this.verify_code,
        errMsg: '短信验证码不能为空'
      }, {
        reg: 'noData',
        val: this.code,
        errMsg: '图形验证码不能为空'
      }]
      if (formCheck(checkItem).result) {
        this.$Api({
          api_name: 'kkl.user.signup',
          mobile: this.mobile,
          verify_code: this.verify_code,
          code: this.code,
          password: this.$MD5.hex_md5(this.pwd),
          again_password: this.$MD5.hex_md5(this.pwd),
          parent_id: this.referee,
          nickname: this.realname
        }, (err, data) => {
          console.log(data, err)
          if (!err) {
            this.$msg('注册成功', 'success', 'middle', 1500)
            setTimeout(() => {
              this.$router.replace({
                path: '/login'
              })
            }, 1500);
            // this.$message({
            //   message: '注册成功',
            //   type: 'success',
            //   duration: ALERT_TIME,
            //   onClose: () => {
            //     this.$router.replace({
            //       path: '/login'
            //     })
            //   }
            // });
          } else {
            if (data.code === 40051) {
              this.img_code = ''
              this.get_img_code()
            }
            this.$msg(err.error_msg, 'cancel', 'middle', 1500)
          }
        })
      } else {
        this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
      }
    },
    getPwd () {
      this.$router.push({
        path: '/login'
      })
    },
  }
}



