
import MescrollVue from 'mescroll.js/mescroll.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import { fiveIndex } from '@/config/mixin.js'
export default {
name: 'exchange',
mixins: [fiveIndex],
data() {
return {
list: [],
show: false,
title: '',
id: '',
mescroll: null, // mescroll实例对象
mescrollDown:{
use: false
},
mescrollUp: { // 上拉加载的配置.
callback: this.upCallback, // 上拉回调,
page: {
num: 0,
size: 10
},
noMoreSize: 5,
htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
},
}
},
components: {
Confirm,
MescrollVue
},
methods: {
mescrollInit (mescroll) {
this.mescroll = mescroll  
},
turn_next(id, cash) {
this.title = '确认兑换' + cash + '元金龙28体验卡'
this.id = id
this.show = true
},
confirm() {
this.$router.push({
path: '/exchangeGoods',
query: {
id: this.id
}
})
},

get_card_list(firstRow, fetchNum) {
this.$Api({
api_name: 'kkl.index.giftCardList',
firstRow: firstRow,
fetchNum: fetchNum
}, (err, data) => {
if (!err) {
let arr = data.data.gift_card_list

if (firstRow === 1) this.list = []

this.list = this.list.concat(arr)
this.$nextTick(() => {
this.mescroll.endSuccess(arr.length)
this.mescroll.endBySize(arr.length, data.data.total)
})
} else {
this.$msg(err.error_msg, 'cancel', 'middle', 1500)
}
})
},

upCallback(page, mescroll) {
this.get_card_list((page.num-1)*page.size, page.size)
},
},
created() {

}
}



