import MescrollVue from 'mescroll.js/mescroll.vue'
export default {
    components:{
        MescrollVue
    },
    name: 'receiveRecord',
    data () {
        return {
            list: [{
                title: '总额',
            },{
                title: '数量',
            },{
                title: '已领取',
            },{
                title: '生成时间',
            },{
                title: '操作',
            }],
            relief_list: [],
            mescroll: null, // mescroll实例对象
            mescrollDown:{
                use: false
            }, 
            mescrollUp: { // 上拉加载的配置.
                callback: this.upCallback, // 上拉回调,
                page: {
                    num: 0, 
                    size: 15
                },
                htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
			},
        }
    },
    created() {
        
    },
    methods: {
        mescrollInit (mescroll) {
            this.mescroll = mescroll  // 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
        },
        // 获取排行榜奖列表
        get_list(firstRow, fetchNum) {
            this.$Api({
                api_name: 'kkl.user.redPacketList',
                firstRow: firstRow,
                fetchNum: fetchNum
            }, (err, data) => {
                if (!err) {
                    let arr = data.data.red_packet_list
			        // 如果是第一页需手动制空列表
			        if (firstRow === 1) this.relief_list = []
			        // 把请求到的数据添加到列表
			        this.relief_list = this.relief_list.concat(arr)
                    this.$nextTick(() => {
                        this.mescroll.endSuccess(arr.length)
                        this.mescroll.endBySize(arr.length, data.data.total)
                    })
                } else {
                    this.$msg(err.error_msg, 'cancel','middle', 1500)
                }
            })
        },
        // 上拉加载
        upCallback(page, mescroll) {
            this.get_list((page.num-1)*page.size, page.size)
        },
        // 撤销红包
        // cancle(id) {
        //     this.$Api({
        //         api_name: 'kkl.user.cancelRedPacket',
        //         red_packet_id: id
        //     }, (err, data) => {
        //         if (!err) {
        //             this.$msg(data.data, 'success', 'middle', 1500)
        //             this.get_list()
        //         } else {
        //             this.$msg(err.error_msg, 'cancel', 'middle', 1500)
        //         }
        //     })
        // },
    }
}



