import {
  getStore
} from '@/config/utils'
export default {
  name: "red-dialog",
  model: {
    prop: "dialog_show",
    event: "change"
  },
  props: {
    dialog_show: {
      type: Boolean,
      default: false
    },
    jiami_id: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      haveLogin: false,
      each_money: 0,
      isSuccess: false,
      haveRed: true
    };
  },
  created() {
    //   获取用户信息
    this.$Api(
      {
        api_name: "kkl.user.getUserInfo"
      },
      (err, data) => {
        if (data.code == 0) {
          this.haveLogin = true;
          // 获取红包详情
          this.$Api(
            {
              api_name: "kkl.index.getRedPacketInfo",
              red_packet_id: this.jiami_id
            },
            (err, data) => {
              if (!err) {
                this.haveRed = true
                this.each_money = data.data.red_packet_info.each_money;
              } else {
                this.haveRed = false
              }
            }
          );
        } else {
          this.haveLogin = false;
        }
      }
    );
  },
  methods: {
    sendPacket () {
      this.$router.push({
        path: '/sendPacket'
      })
    },
    closeDialog() {
      this.$emit("change", false);
    },
    getRedDialog() {
      this.$Api(
        {
          api_name: "kkl.index.getRedPacket",
          red_packet_id: this.jiami_id
        },
        (err, data) => {
          if (data.code == 0) {
            this.$toast({text: '领取成功'});
            this.isSuccess = true
            this.$native.native_clean_url();
            setTimeout(() => {
              this.$emit("change", false);
            }, 1500);
          } else {
            this.$msg(err.error_msg, "cancel", "middle", 1500);
          }
        }
      );
    },
    goLogin() {
      // 如果是h5
      let is_app = getStore('is_app')
      if (is_app && Number(is_app) == 0) {
        this.$router.replace({
          path: 'login'
        });
      } else {
        this.$native.native_login();
      }
      
    }
  }
};



