import { formCheck} from '@/config/checkout'
export default {
    components:{
        
    },
    name: 'exchangeGoods',
    data () {
        return {
            bill_list: [{
                num: '',
                content: '7天内流水'
            },{
                num: '',
                content: '7天内已提'
            },{
                num: '',
                content: '7天内充值'
            }],
            list: [{
                title: '手机短信'
            },
            // {
            //     title: '微信验证'
            // },
            ],
            currentIndex: 0, // 索引
            cash: '', // 现金
            money: '', // 兑换乐豆
            count: '', // 兑换数量
            safe_pwd: '', // 安全密码
            verify_code: '', //短信验证码
            sendAuthCode: false,
            auth_time: 0,
            mobile: ''
        }
    },
    created() {
        this.$Api({
            api_name: 'kkl.index.giftCardInfo',
            gift_card_id: this.$route.query.id
        }, (err, data) => {
            if (!err) {
                let res = data.data.gift_card_info
                this.cash = res.cash
                this.bill_list[0].num = res.flow
                this.bill_list[1].num = res.deposit
                this.bill_list[2].num = res.recharge
                this.money = res.money
            } else {
                this.$msg(err.error_msg, 'cancel', 'middle', 1500)
            }
        })
        this.get_user_info()
    },
    methods: {
        // 获取银行余额
        get_user_info() {
            this.$Api({
                api_name: 'kkl.user.getUserInfo',
            }, (erra, res) => {
                if (!erra) {
                    this.mobile = res.data.mobile
                } else {
                    this.$msg(erra.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        // 发送验证码
        get_verify() {
            let checkItem = [{
                reg: 'mobile',
                val: this.mobile,
                errMsg: '请输入正确的手机号'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.user.sendVerifyCode',
                    mobile: this.mobile
                }, (err, data) => {
                    if (!err) {
                        this.$msg(data.data, 'success', 'middle', 1500)
                        this.sendAuthCode = true
                        this.auth_time = 60
                        var auth_timetimer =  setInterval(()=>{
                            this.auth_time--;
                            if(this.auth_time<=0){
                                this.sendAuthCode = false
                                clearInterval(auth_timetimer);
                            }
                        }, 1000);
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        },
        change_index(index) {
            this.currentIndex = index
        },
        // 兑换卡券
        exchange_now() {
            let checkItem = [{
                reg: 'noData',
                val: this.count,
                errMsg: '兑换数量还不能为空'
            }, {
                reg: 'noData',
                val: this.safe_pwd,
                errMsg: '安全密码不能为空'
            }, {
                reg: 'noData',
                val: this.verify_code,
                errMsg: '验证码不能为空'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.index.exChangeCard',
                    safe_password: this.$MD5.hex_md5(this.safe_pwd),
                    number: this.count,
                    verify_code: this.verify_code,
                    gift_card_id: this.$route.query.id
                }, (err, data) => {
                    if (!err) {
                        this.$msg(data.data, 'success', 'middle', 1500)
                        this.safe_pwd = ''
                        this.count = ''
                        this.verify_code = ''
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        },
    }
}



