export default {
  name: "userInfo",
  data () {
    return {
      info: {}
    }
  },
  methods: {
    getInfo () {
      this.$Api(
        {
          api_name: "kkl.user.getUserHomeInfo"
        },
        (err, data) => {
          if (!err) {
            this.info = data.data
          }
        }
      );
    }
  },
  mounted () {
    this.getInfo()
  }
}



