export default {
  components: {},
  name: "exeBean",
  data() {
    return {
      exe_bean: "", // 乐豆数量
      content: "", // 富文本
      more_exp: "" // 可兑换经验
    };
  },
  created() {
    this.get_user_info();
  },
  methods: {
    get_user_info() {
      this.$Api(
        {
          api_name: "kkl.user.getUserInfo"
        },
        (err, res) => {
          console.log('getUserInfo', err, res);
          if (!err) {
            this.more_exp = res.data.more_exp;
          } else {
            this.$msg(err.error_msg, "cancel", "middle", 1500);
          }
        }
      );
    },
    exchange() {
      this.$Api(
        {
          api_name: "kkl.user.exChangeExp",
          bean: this.exe_bean
        },
        (err, data) => {
          if (!err) {
            this.$msg(data.data, "success", "middle", 1500);
          } else {
            this.$msg(err.error_msg, "cancel", "middle", 1500);
          }
        }
      );
    }
  }
};



