export default {
    components:{
        
    },
    name: 'confirmLogin',
    data () {
        return {
           pwd: '',
        }
    },
    created() {
        
    },
    methods: {
        turn_center() {
            if (this.pwd != '') {
                this.$Api({
                api_name: 'kkl.user.checkPassword',
                safe_password: this.$MD5.hex_md5(this.pwd),
            }, (err, data) => {
                if (!err) {
                    this.$native.native_show_tabBar(4)
                } else {
                    this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                }
            })
            } else {
                    this.$msg('请填写安全密码', 'cancel', 'middle', 1500)
            }
        }
    }
}



