import { formCheck } from '@/config/checkout'
export default {
    components:{
        
    },
    name: 'myBank',
    data () {
        return {
            list: [{
                title: '存乐豆'
            },{
                title: '取乐豆'
            }],
            currentIndex: 0,
            bean: '',
            take_bean: '',
            bean_count: '', 
            get_bean: '',
            bank_pwd: ''
        }
    },
    created() {
        this.get_user_info()
    },
    methods: {
        get_user_info() {
            this.$Api({
                api_name: 'kkl.user.getUserInfo',
            }, (erra, res) => {
                if (!erra) {
                    let data= res.data
                    this.bean = data.left_money
                    this.take_bean = data.frozen_money
                } else {
                    this.$msg(erra.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        change_index(index) {
            this.currentIndex = index
        },
        // 存储乐豆
        save() {
            if (this.bean_count != '') {
                this.$Api({
                    api_name: 'kkl.user.accessBean',
                    type: 1,
                    number: this.bean_count
                }, (err, data) => {
                    if (!err) {
                        this.bean_count = ''
                        this.$msg(data.data, 'success', 'middle', 1500)
                        this.get_user_info()
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg('请填写存入数量', 'cancel', 'middle', 1500)
            }
        },
        // 取出乐豆
        take() {
            let checkItem = [{
                reg: 'noData',
                val: this.get_bean,
                errMsg: '请输入取出乐豆数量'
            }, {
                reg: 'noData',
                val: this.bank_pwd,
                errMsg: '请输入密码'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.user.accessBean',
                    type: 0,
                    number: this.get_bean,
                    bank_password: this.$MD5.hex_md5(this.bank_pwd),
                }, (err, data) => {
                    if (!err) {
                        this.get_bean = ''
                        this.bank_pwd = ''
                        this.$msg(data.data, 'success','middle', 1500)
                        this.get_user_info()
                    } else {
                        this.$msg(err.error_msg, 'cancel','middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle',1500)
            }
        },
        forget_pwd() {
            this.$router.push({
                path: '/retrievePwd',
                query: {
                    type: 3
                }
            })
        },
        editor_pwd() {
            this.$router.push({
                path: '/bankPwd'
            })
        }
    }
}



